*,
*::after,
*::before {
    margin: 0;
    box-sizing: border-box;
    padding: 0;
    text-decoration: none;
    font-family: 'Montserrat', sans-serif;
}

html {
    background-color: #000;
    min-height: 100vh;
}

.absoluteCenter {
    display: flex;
    justify-content: center;
    align-items: center;
}

.container {
    margin: 0 auto;
    width: 1200px;
}

/* Header */

header {
    position: fixed;
    width: 100%;
    z-index: 100;
    transition: .5s;
    background: black;

    color: white;
}

.autorisation {
    position: absolute;
    top: 17px;
    right: 30px;

    width: 40px;
    height: 40px;

    cursor: pointer;
}

#header {
    display: flex;
    justify-content: space-between;
    font-size: 30px;
    height: 80px;
}

.header-nav {
    justify-content: space-around;
    width: 60%;
}

.header-link {
    color: #fff;
    font-size: 20px;
}

.header-link:hover {
    color: #00EAC0;
    transition: .5s;
}

.header-link:hover > .header-link::after {
    width: 100%;
    height: 4px;
    background: white;
    transition: .5s;
}

.logo {
    width: 300px;
}



/* Main */
.main-background {
    min-height: 100vh;
    
    background: #0f0f10;
}

#main-info {
    flex-direction: column;
    align-items: baseline;
}

.infoBlock {
    display: flex;
    justify-content: space-between;
    width: 80%;
    min-height: 200px;
    border-radius: 20px;
    color: white;
    margin: 0 auto;
    margin-bottom: 100px;
}

.titleText {
    margin-bottom: 30px;
}

.infoText {
    font-size: 20px;
    font-weight: normal;
}

p {
    cursor: pointer;
    margin-top: 2vw;
    /*text-shadow: 0 0 20px #fefcc9, 10px -10px 30px #feec85, -20px -20px 40px #ffae34, 20px -40px 50px #ec760c, -20px -60px 60px #cd4606, 0 -80px 70px #973716, 10px -90px 80px #451b0e;*/
}

.show-hide {
    font-size: 20px;
    color: #FF3EF7;
}

.preview {
    flex-direction: column;
}

.photoZone {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
}

.Drugphoto {
    position: relative;
    margin-top: 50px;
    width: 27%;
    min-height: 15vw;
    border-radius: 20px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.Drugphoto > img {
    width: 100%;
    border-radius: 20px;
}

#btn {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: 16px;
    font-weight: 700;
    width: 400px;
    height: 80px;
    border-radius: 40px;
    background: #039BE5;
    cursor: pointer;
}

#btn > h1 {
    text-align: center;
    font-size: 22px;
    min-width: 75%;
}

.contact-block {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 80%;
    transition: .5s;
    height: 85vh;
    margin: 0 auto;
}

.contact-block-text {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 50%;
    height: 30vh;
}

.contact-title {
    font-size: 50px;
    color: #fff;
}

.contact-logo {
    width: 500px;
    height: 500px;
    background-image: url("../components/images/Subtract.png");
    background-size: cover;
    background-position: center;
    border-radius: 10px;
}

#btn > .logo {
    width: 50px;
}



/* News */

.pos-rel {
    position: relative;
}

.newsBlock {
    position: absolute;
    justify-content: space-around;
    padding: 10px;
    padding-top: 0;
    flex-wrap: wrap;
    top: 60px;
    left: 0;
    background: rgb(19, 19, 19);
    z-index: 2;
}

.newsBlock > div {
    cursor: pointer;
    margin-top: 15px;
}

.dark-text {
    position: relative;
    padding: 0px 20px 0px 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 100px;
}

.news-block {
    height: 300px;
    border-radius: 20px;
    background: black;

    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.news-block-large {
    width: 66%;
}

.news-block-small {
    width: 32.5%;
}

.news-title {
    font-size: 20px;
    text-shadow: 2px 2px 4px #000;
}

.likes-dislikes {
    display: flex;
    font-size: 10px;
}

.likes-dislikes > div > .logo {
    width: 15px;
    cursor: pointer;
}

.likes {
    margin: 0px 30px 0px 7px;
    font-family: normal;
    font-weight: 100;
}

.pill-news {
    z-index: 1;
}

.pill-news > img {
    width: 50px;
}

#large-pill > img {
    transform: rotate(57deg);
    width: 110px;
}

/* Laws */

#first-law {
    margin-top: 90px;
}

.laws-title {
    display: flex;
    align-items: center;
    width: 100%;
    height: 100px;
    color: #FF3EF7;
    font-size: 30px;
    font-weight: bold;
    padding: 20px;
}

.laws-main {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    width: 100%;
}

.lawLi {
    flex-direction: column;
    margin-top: 0;
}

ol {
    list-style: none;
}

.law-list {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 30px;
}

.list-number {
    color: #FF3EF7;
    font-weight: bold;
    font-size: 30px;
}

.law-info {
    width: 95%;
    color: #00EAC0;
    font-size: 18px;
}

.lawName {
    width: 100%;
    min-height: 50px;
    text-align: center;
    color: #FF3EF7;
    font-size: 30px;
    font-weight: bold;
    margin-bottom: 20px;
}

/* Form */

.formZone {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.241);
    z-index: 1000;
}

.form {
    display: flex;
    position: relative;
    flex-direction: column;
    gap: 10px;
    padding-left: 2em;
    padding-right: 2em;
    padding-bottom: 0.4em;
    background-color: #171717;
    border-radius: 20px;
    
  }
  
  #heading {
    text-align: center;
    margin: 2em;
    color: rgb(255, 255, 255);
    font-size: 1.2em;
  }
  
  .field {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5em;
    border-radius: 25px;
    padding: 0.6em;
    border: none;
    outline: none;
    color: white;
    background-color: #171717;
    box-shadow: inset 2px 5px 10px rgb(5, 5, 5);
  }
  
  .input-icon {
    height: 1.3em;
    width: 1.3em;
    fill: #00ffff;
  }
  
  .input-field {
    background: none;
    border: none;
    outline: none;
    width: 100%;
    color: rgb(255, 255, 255);
  }
  
  .form .btn {
    display: flex;
    justify-content: center;
    flex-direction: row;
    margin-top: 2.5em;
  }
  
  .button1 {
    padding: 0.5em;
    padding-left: 1.1em;
    padding-right: 1.1em;
    border-radius: 5px;
    margin-right: 0.5em;
    border: none;
    outline: none;
    transition: .4s ease-in-out;
    background-image: linear-gradient(163deg, #00ffff 10%, #6423ff 90%);
    color: rgb(255, 255, 255);
  }
  
  .button1:hover {
    background-image: linear-gradient(163deg, #9b41fc 20%,#e74efff1 80%);
    color: #ffffff;
    box-shadow: 1px 1px 10px 1px #ffffff;
  }
  
  .button2 {
    padding: 0.5em;
    padding-left: 2.3em;
    padding-right: 2.3em;
    border-radius: 5px;
    border: none;
    outline: none;
    transition: .4s ease-in-out;
    background-image: linear-gradient(163deg,#13bded 0%,#6423ff 100%);
    color: rgb(255, 252, 252);
  }
  
  .button2:hover {
    background-image: linear-gradient(163deg, #9b41fc 20%, #e74efff1 80%);
    color: #ffffff;
    box-shadow: 1px 1px 10px 1px #ffffff;
  }
  
  .button3 {
    margin-bottom: 3em;
    padding: 0.5em;
    border-radius: 5px;
    border: none;
    outline: none;
    transition: .4s ease-in-out;
    background-image: linear-gradient(163deg, #0891b2 0%, #6423ff 100%);
    color: rgb(255, 255, 255);
  }
  
  .button3:hover {
    background-image: linear-gradient(163deg, #a00000fa 0%, #d10050 100%);
    color: rgb(255, 255, 255);
  }
  
  .card {
    background-image: linear-gradient(163deg, #13bded 0%, #3700ff 100%);
    border-radius: 22px;
    transition: all .3s;
  }
  
  .card2 {
    border-radius: 0;
    transition: all .2s;
  }
  
  .card2:hover {
    transform: scale(0.98);
    border-radius: 20px;
  }
  
  .card:hover {
    box-shadow: 0px 0px 30px 1px #6423ff;
  }

  .closeForm {
    position: absolute;
    top: 10px;
    right: 15px;
  }

/* Stories */

.storiesMain {
    display: flex;
    flex-direction: column;
    justify-content: space-around;

    padding-top: 100px;
    

    min-height: 100vh;

    color: white;
}

.storiesBlock {
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding-bottom: 40px;
    width: 100%;
    min-height: 500px;
    border-radius: 20px;
    margin-bottom: 50px;
    box-shadow: 0px 0px 20px black inset;
}

.story-text {
    width: 90%;
    min-height: 500px;
}

.story-title {
    width: 100%;
    height: 100px;

    font-size: 30px;
    font-weight: bold;
    color:#FF3EF7 ;
}

.story-info {
    color:#00EAC0;
    font-size: 18px;
}

.quote {
    color:#FF3EF7 ;
    font-size: 23px;
    font-weight: bold;
    font-style: italic;
}

/* Forum */

.forumElem {
    display: flex;
    justify-content: space-between;

    width: 100%;
    min-height: 100vh;
    padding: 100px;
    padding-top: 120px;
}

.forum-info {
    display: flex;
    flex-direction: column;
    justify-content: center;

    width: 65%;
    min-height: 600px;

    box-shadow: 0px 0px 10px black inset;
}

.forum-nav {
    display: flex;
    flex-direction: column;

    width: 30%;
    min-height: 600px;

    box-shadow: 0px 0px 10px black inset;
}

ul {
    list-style: none;
    display: flex;
    justify-content: space-around;
    color: #00EAC0;
    font-size: 20px;
    width: 100%;
}

ul > li {
    text-decoration: none;
    text-align: center;
    width: 32%;
    font-size: 17px;
    padding: 15px;
    cursor: pointer;
}

.forum-nav-list {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;

    background: #121212;
    box-shadow: 0px 0px 10px black inset;
}

.forum-tree {
    flex-direction: column;
    width: 70%;
    margin: 100px auto;
}

.forum-tree > li {
    
}

/* Footer */

footer {
    width: 100%;
    min-height: 40vh;
    margin-top: 50px;
    background: #0f0f10;
}

/* Media */

.mobile {
    display: none;
}

@media (max-width: 1343px) {
    .container {
        width: 1000px;
    }
}

@media (max-width: 1180px) {
    .container {
        width: 767px;
    }

    .logo {
        width: 150px;
    }

    .header-link {
        font-size: 15px;
    }
}

@media (max-width: 760px) {
    header {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 120px;
        width: 100%;
    }
    #header {
        display: none;
    }
    .autorisation {
        display: none;
    }
    .mobile-header {
        display: flex;
        justify-content: space-between;
        width: 80%;
    }
    .mobile-header > div > img {
        width: 200px;
    }
    .mobile-burger {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 70px;
        height: 70px;
        border: 4px solid white;
        border-radius: 50%;
    }
    .titleText { 
        width: 120%;    
    }
    .limit { 
        min-height: 300px;
    }
    .container {
        width: 80%;
    }
    .infoBlock > .photo {
        display: none;
    }
    .information {
        width: 100%;
    }
    .mobile-burger > svg {
        width: 60%;
        height: 70px;
    }
}